body {
  text-align: center;
  margin: 50px auto;
  max-width: 768px;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

/* Full-width input fields */
input[type='email'],
input[type='password'] {
  width: 50%;
  padding: 15px;
  margin: 5px 0 2px 0;
  display: inline-block;
  border: none;
  background: #f1f1f1;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

input[type='email']:focus,
input[type='password']:focus {
  background-color: #ddd;
  outline: none;
}

input:focus:invalid {
  outline: none;
  border-color: #900;
  background-color: #fdd;
}

hr {
  border: 1px solid #f1f1f1;
  margin-bottom: 35px;
}

/* Set a style for all buttons */
button {
  background-color: #4caf50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 50%;
  opacity: 0.9;
}

button:hover {
  opacity: 1;
}

/* Add padding to container elements */
.container {
  padding: 16px;
}

.loginbtn {
  font-size: 1em;
}

.error {
  width: 100%;
  padding: 0;

  font-size: 80%;
  color: #900;
  border-radius: 0 0 5px 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.error.active {
  padding: 0.3em;
}
span {
  display: block;
}
span {
  margin: 0px auto 10px;
}



